@import "../../styles/responsive";

.container {
  padding: 8% 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &::before {
    content: '';
    width: 17rem;
    height: 17rem;
    position: absolute;
    left: -10%;
    top: 40%;
    background: #D9EFFF;
    transform: rotate(13.796deg);
    border-radius: 23px;
  }

  &::after {
    content: '';
    width: 15rem;
    height: 15rem;
    position: absolute;
    right: -17%;
    top: 10%;
    border-radius: 50%;
    border: 90px solid rgba(255, 184, 46, 0.18);
  }

  .subText {
    color: #9090a0;
    line-height: 2rem;
  }
  .mr5 {
    margin-right: 1rem;
  }
  .select {
    width: 30%;
    margin: 1%;
  }

  .planCards {
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    flex-direction: row;
    // flex-wrap: wrap;
    text-align: center;
    justify-content: space-around;
  }


  @include mobile {
    padding-top: 30%;

    h2 {
      font-size: 1.1rem;
    }

    .mr5 {
      margin-right: 0;
      margin-bottom: 1rem;
    }

    .select {
      width: 100%;
      flex-wrap: wrap;
    }

    &::after, &::before {
      width: 0;
      height: 0;
      border: 0px;
    }
    .planCards { 
      flex-wrap: wrap;
    }
  }

  @include mobileLandscape { 
    padding-top: 15%;
    .select { 
      width: 80%;
    }
    &::after, &::before {
      width: 0;
      height: 0;
      border: 0px;
    }
    .planCards { 
      flex-wrap: wrap;
    }
  }

  @include tablet {
    .select { 
      width: 80%;
    }

    .planCards { 
      flex-direction: column;
    }

    &::after, &::before {
      width: 0;
      height: 0;
      border: 0px;
    }
  }

}

.mcontainer {
  padding-top: 30%;
  margin: 9%;
  
  .backText {
    color: #5E5E6C;
    text-align: left;
    font-size: 0.9rem;
    margin: 5% 0;
    &::before {
      content: " < ";
    }
  }

  .amount {
    font-size: 1.5rem;
    margin: 5% 0;
    text-align: center;
  }

  .hrLine {
    width: 85%;
    border: 0.5px solid #dadaec;
  }

  .subCard {
    border: 1px solid #dcdcf8;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    // margin: 0.5rem;
    border-radius: 1rem;
    line-height: 2.5rem;
    font-size: 0.9rem;
    background-color: white;
    margin-bottom: 5%;

    .title {
      color: #202021;
      font-size: 1.1rem;
    }

    .subText {
      color: #5e5e6c;
    }
  }


  @include tablet {
    padding-top: 8%;

    .amount {
      margin: 0;
    }

  }
}
