@import "../../styles/variables";
@import "../../styles/colors";

.container {
  display: inline-flex;
  position: relative;
  padding-left: 35px;
  align-items: center;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;


  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .checkmark {
    position: absolute;
    top: 18%;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: white;
    border: 2px solid #eee;
    border-radius: 5px;
  }

  input:checked ~ .checkmark {
    background-color: #57BA47;
    border-color:  #57BA47;
  }

  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  input:checked ~ .checkmark:after {
    display: block;
  }

  .checkmark:after {
    left: 7px;
    top: 3px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  &.radio {
    .checkmark { 
      border-radius: 50%;
    }

    input:checked ~ .checkmark { 
      background-color: $secondary;
      border-color: $secondary;
    }
  } 
}
