@import "./../../../styles/colors";
@import "./../../../styles/responsive";


.container {
  display: flex;
  width: 20rem;
  border: 1px solid #d1d1e4;
  border-radius: 20px;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
  margin: 1rem;
  cursor: pointer;
  position: relative;

  .checkbox {
    position: absolute;
    left: 15px;
    top: 0;
  }

  &.active {
    border-color: $secondary;
    background: rgba(88, 88, 198, 0.11);
  }

  .head {
    color: #5e5e6c;
    margin: 1rem;
    font-size: 1.5rem;
  }

  .hrLine {
    width: 85%;
    border: 0.5px solid #dadaec;
  }

  .subCard {
    border: 1px solid #dcdcf8;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0.5rem;
    border-radius: 1rem;
    line-height: 2.5rem;
    font-size: 0.9rem;
    background-color: white;

    .title {
      color: #202021;
      font-size: 1.1rem;
    }

    .subText {
      color: #5e5e6c;
    }
  }

  .more {
    font-size: 0.8rem;
    color: #5e5e6c;
    text-decoration: underline;
    cursor: pointer;
  }
  .amount {
    font-size: 2rem;
  }
  .regBtn {
    font-size: 0.8rem;
  }

  .xShow {
    display: none;
  }

  .LShow {
    display: block;
  }

    @include mobile {
    max-width: 18rem;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0.5rem;
    justify-content: center;

    .viewCont {
      width: auto;
    }

    .xShow { 
      display: block;
    }

    .LShow {
      display: none;

    }

    img {
      width: 2rem;
    }

    .head {
      font-size: 1rem;
    }

    .subCard {
      width: 45%;
      margin: 5px;
      .hrLine {
        border: 0;
      }

      & > div:last-child {
        display: none;
      }
    }

    .amount {
      font-size: 1rem;
      margin-left: 10%;
    }

    .checkbox {
      left: 4px;
    top: -10px;
    }
  }
  @include tablet {
    // max-width: 18rem;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 0.5rem;
    justify-content: center;

    .viewCont {
      width: auto;
    }

    .xShow { 
      display: block;
    }

    .LShow {
      display: none;

    }

    img {
      width: 2rem;
    }

    .head {
      font-size: 1rem;
    }

    .subCard {
      width: 45%;
      margin: 5px;
      .hrLine {
        border: 0;
      }

      & > div:last-child {
        display: none;
      }
    }

    .amount {
      font-size: 1rem;
      margin-left: 10%;
    }

    .checkbox {
      left: 4px;
    top: -10px;
    }
  }

  @include mobile {
    max-width: 18rem;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0.5rem;
    justify-content: center;

    .viewCont {
      width: auto;
    }

    .xShow { 
      display: block;
    }

    .LShow {
      display: none;

    }

    img {
      width: 2rem;
    }

    .head {
      font-size: 1rem;
    }

    .subCard {
      width: 45%;
      margin: 5px;
      .hrLine {
        border: 0;
      }

      & > div:last-child {
        display: none;
      }
    }

    .amount {
      font-size: 1rem;
      margin-left: 10%;
    }

    .checkbox {
      left: 4px;
    top: -10px;
    }
  }
}
